
.cartcontrol {
  font-size: 0;
}
.cartcontrol .cart-count {
    display: inline-block;
    vertical-align: top;
    width: 12px;
    padding-top: 6px;
    line-height: 24px;
    text-align: center;
    font-size: 10px;
    color: #93999f;
}
.cartcontrol .cart-decrease {
    display: inline-block;
    padding: 6px;
    -webkit-transition: all 0.4s linear;
    transition: all 0.4s linear;
}
.cartcontrol .cart-decrease .inner {
      display: inline-block;
      line-height: 24px;
      font-size: 24px;
      color: #00a0dc;
}
.cartcontrol .cart-decrease.move-enter-active, .cartcontrol .cart-decrease.move-leave {
      opacity: 1;
      -webkit-transform: translate3D(0, 0, 0);
              transform: translate3D(0, 0, 0);
}
.cartcontrol .cart-decrease.move-enter-active .inner, .cartcontrol .cart-decrease.move-leave .inner {
        -webkit-transform: rotate(0);
                transform: rotate(0);
}
.cartcontrol .cart-decrease.move-enter, .cartcontrol .cart-decrease.move-leave-active {
      opacity: 0;
      -webkit-transform: translate3d(24px, 0, 0);
              transform: translate3d(24px, 0, 0);
}
.cartcontrol .cart-decrease.move-enter .inner, .cartcontrol .cart-decrease.move-leave-active .inner {
        -webkit-transform: rotate(180deg);
                transform: rotate(180deg);
}
.cartcontrol .cart-add {
    display: inline-block;
    padding: 6px;
    line-height: 24px;
    font-size: 24px;
    color: #00a0dc;
}



